import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { Container, Grow, Grid, Paper, AppBar, TextField, Button } from '@material-ui/core';
import { Link, useHistory, useLocation } from 'react-router-dom/cjs/react-router-dom';
import ChipInput from 'material-ui-chip-input';

import Posts from '../Posts/Posts';
import Form from '../Form/Form';
import Paginate from '../Pagination';
import { getPosts, getPostsBySearch } from '../../actions/posts';

import useStyles from './styles' ;

function useQuery() {
    return new URLSearchParams(useLocation().search);
    /**
     * https://stackoverflow.com/questions/35352638/how-to-get-parameter-value-from-query-string/53079875#53079875
     */
}


const Home = () => {
    const dispatch = useDispatch();
    const [currentId, setCurrentId] = useState(null);
    const query = useQuery();
    const history = useHistory();
    const page = query.get('page') || 1;
    const searchQuery = query.get('searchQuery');
    const classes = useStyles() ;
    const [search,setSearch] = useState('') ;
    const [tags,setTags] = useState([]) ;

    const handleKeyPress = (e)=>{
        if(e.keyCode === 13){
            searchPost() ;
        }
    }

    const handleAdd = (tag)=> setTags([...tags , tag]) ; 
    const handleDelete = (tagToDelete)=> setTags(tags.filter((tag)=>tag!==tagToDelete)) ; 

    const searchPost = ()=>{
        if(search.trim() || tags){
            dispatch(getPostsBySearch({ search, tags: tags.join(',') }));
            history.push(`/posts/search?searchQuery=${search || 'none'}&tags=${tags.join(',')}`);
        }
        else{
            history.push('/') ;
        }
    }

    return (
        <Grow in>
            <Container maxWidth='xl'>
                <Grid container justifyContent='space-between' alignItems='stretch' spacing={4} className={classes.gridContainer}>
                    <Grid item xs={12} sm={6} md={8}>
                        <Posts setCurrentId={setCurrentId} />
                    </Grid>
                    <Grid item xs={12} sm={6} md={4}>
                        <AppBar className={classes.appBarSearch} position='static' color='inherit'>
                            <TextField
                                name='search'
                                variant='outlined'
                                label='Search Memories'
                                fullWidth
                                value={search}
                                onChange={(e)=>{ setSearch(e.target.value) }}
                                onKeyPress={handleKeyPress}
                            />
                            <ChipInput 
                                style={{margin:'10px 0'}}
                                value={tags}
                                onAdd={handleAdd}
                                onDelete={handleDelete}
                                label='Search Tags'
                                variant='outlined'
                            />
                            <Button onClick={searchPost} className={classes.search} variant='contained' color='primary'>Search</Button>
                        </AppBar>
                        <Form currentId={currentId} setCurrentId={setCurrentId} />
                        <Paper elevation={6} className={classes.pagination} >
                            <Paginate page={page}/>
                        </Paper>
                    </Grid>
                </Grid>
            </Container>
        </Grow>
    )
}

export default Home